import React from "react"
import Image from "next/image"

export function FeaturesSection() {
  return (
    <section className="container py-12">
      <h2 className="text-2xl md:text-3xl font-bold text-center mb-8">POS Solution Features</h2>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-8 items-center">
        <div>
          <Image
            src="/images/wallet-app.png"
            width={400}
            height={800}
            alt="POS App Interface"
            className="w-full max-w-md mx-auto"
          />
        </div>

        <div className="space-y-6">
          <div className="flex items-start gap-4">
            <div className="flex-shrink-0 w-8 h-8 rounded-full bg-blue-100 flex items-center justify-center">
              <span className="text-blue-500 font-semibold">1</span>
            </div>
            <div>
              <h3 className="text-lg font-semibold mb-2">Cash/Receipt Management Tools</h3>
              <p className="text-gray-600">
                Track your transactions and manage your cash flow with our easy-to-use receipt management tools.
              </p>
            </div>
          </div>

          <div className="flex items-start gap-4">
            <div className="flex-shrink-0 w-8 h-8 rounded-full bg-blue-100 flex items-center justify-center">
              <span className="text-blue-500 font-semibold">2</span>
            </div>
            <div>
              <h3 className="text-lg font-semibold mb-2">High Performance Device</h3>
              <p className="text-gray-600">
                Fast and reliable POS device that ensures smooth transactions and quick processing times.
              </p>
            </div>
          </div>

          <div className="flex items-start gap-4">
            <div className="flex-shrink-0 w-8 h-8 rounded-full bg-blue-100 flex items-center justify-center">
              <span className="text-blue-500 font-semibold">3</span>
            </div>
            <div>
              <h3 className="text-lg font-semibold mb-2">Contactless (NFC Payments)</h3>
              <p className="text-gray-600">
                Accept contactless payments quickly and securely with built-in NFC technology.
              </p>
            </div>
          </div>

          <div className="flex items-start gap-4">
            <div className="flex-shrink-0 w-8 h-8 rounded-full bg-blue-100 flex items-center justify-center">
              <span className="text-blue-500 font-semibold">4</span>
            </div>
            <div>
              <h3 className="text-lg font-semibold mb-2">Printed Receipt for All Transactions</h3>
              <p className="text-gray-600">
                Automatically generate and print receipts for all transactions with your business details.
              </p>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}

